import React from 'react'
import logo from '../logo.svg';
import { connect } from 'react-redux'
import { day1SetName } from '../redux/actions/Day1Actions'

const Day1content = ({ dispatch, name }) => {
  const setName = val => {
    dispatch(day1SetName(val))
  }

  return (
    <div className="Day1">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn 
        </a>
      <input
      placeholder = "Name please.."
      value = { name }
      onChange = {e => setName(e.target.value) }
      />

      {
        name && name === 'Angelo' ?
        <div> Angelo Lance Quetua </div> :
        <div> No one else </div>
      }
      </header>
    </div>
  );
}

const mapStateToProps = state => {
  return {
    name: state.Day1Reducer.name
  }
}

export default connect(mapStateToProps)(Day1content);